import React from 'react';
import { string, shape } from 'react-proptypes';

import { SignupForm } from '../SignupForm';

import './Skill.scss';

const Skill = ({ title, icon, color, description }, index) => {
    const st = {gridArea: `c${index}`};

    return (
        <div className="skill__container"
            key={index} 
            style={st} >

            <div className="skill__header">
                <i className="material-icons"
                    style={{ color }}>
                    {icon}
                </i>

                <h2 className="small">
                    {title}
                </h2>
            </div>

            <p className="copytext-small">
                {description}
            </p>
        </div>
    );
}

Skill.propTypes = {
};

export default Skill;



